import * as couponService from '@/services/couponService';

export default {
  namespace: 'couponIssueData',
  state: {
    issuer: [],
    couponList: [],
    couponQty: 0,
    beginDate: '',
    endDate: '',
  },
  reducers: {
    saveIssuer(state: { issuer: any }, { payload: { data } }) {
      state.issuer = data;
      return { ...state };
    },
    saveCouponList(state: { couponList: any }, { payload: { data } }) {
      state.couponList = data;
      return { ...state };
    },
  },

  effects: {
    *fetchCouponList(payload, { put, call }) {
      const result = yield call(couponService.getCouponList, payload);
      if (result) {
        yield put({
          type: 'saveCouponList',
          payload: result.result || {},
        });
      } else {
        console.log('get coupon list failed!');
      }
    },
  },

  subscriptions: {
    // setup({dispatch}) {
    //   dispatch({type: 'fetchIssuer'})
    // }
  },
};
