import { extend } from 'umi-request';
import { message } from 'antd';

const request = extend({
  //prefix: 'http://vanguard-coupon-plugin:19005',
  timeout: 10000,
  errorHandler,
  headers: {
    // 本地测试用
    //x_tenant_id: localStorage.getItem('CL_TENANT_ID') | 0,
  },
});

function errorHandler(error) {
  const codeMap = {
    500: '系统异常',
    404: '请求无效',
    403: '请求403',
  };
  if (error.response) {
    let status = codeMap[error.response.status];
    if (status) {
      message.warning('网络繁忙，请稍后再试！');
    } else {
      message.warning('服务异常，请稍后再试！');
    }
  } else {
    if (error.type === 'Timeout') {
      message.warning('网络超时，请稍后再试！');
    }
  }
}

// request拦截器, 改变url 或 options.
request.interceptors.request.use((url, options) => {
  return {
    url,
    options: { ...options, interceptors: true },
  };
});

// 局部拦截器使用
request.interceptors.response.use((response, options) => {
  return response;
});

export default request;
