import styles from './index.less';
import { Col, InputNumber, message, Row, Select, Spin } from 'antd';
import debounce from 'lodash/debounce';
import React from 'react';
import * as couponService from '@/services/couponService';
import * as moment from 'moment';
import 'moment/locale/zh-cn';

const { Option } = Select;

const timeFormat = 'YYYY-MM-DD HH:mm:ss';
// webhook ，传递YYYY-MM-DD转换的时候会变成YYYY\\\MM-DD,导致后端报错
const dateFormat = 'LL';

const buIdList = [
  { key: '', value: '全部' },
  { key: '12', value: '华北' },
  { key: '13', value: '西北' },
  { key: '15', value: '东北' },
  { key: '16', value: '华东' },
  { key: '17', value: '苏果' },
  { key: '18', value: '华南' },
  { key: '19', value: 'OLE' },
  { key: '31', value: 'JV华东' },
  { key: '32', value: 'JV东北' },
  { key: '88', value: '全国' },
  { key: '20', value: '购物中心' },
];

/**
 * 优惠券对象
 */
export interface Coupon {
  // 优惠券类型id
  couponsType: string;
  // 优惠券名称
  couponName: string;
  // 主题
  topic: string;
  // 优惠券面额
  cpValue: string;
  // 发券开始时间
  printBeginDate: string;
  // 发券结束时间
  printEndDate: string;
  // 使用开始日期
  beginDate: string;
  // 使用结束日期
  endDate: string;
  // 是否绑定会员使用
  vipFlag: string;
  // 限制发券数量
  limitQty: string;
  // 已发券数量
  issueQty: string;
  // 营销活动ID
  campaignId: string;
}

class CouponIssue extends React.Component {
  lastFetchId: number;
  getCouponList: any;
  // 查询的优惠券列表结果
  queryedCouponList: Coupon[] = [];

  constructor(props) {
    super(props);
    this.lastFetchId = 0;
    this.getCouponList = debounce(this.fetchCouponList, 800);
  }

  state = {
    // 优惠券列表
    couponList: [],
    // bu编码，默认按全部查询
    buIdQuery: '',
    // 优惠券面额查询字段
    cpValueQuery: '',
    // 优惠券查询类型，默认按优惠券ID查询
    couponQueryType: 'couponType',
    // 优惠券查询值
    couponQueryValue: '',
    // 优惠券类型id
    couponsType: '',
    // 优惠券名称
    couponName: '',
    // 优惠券数量
    couponQty: '',
    // 优惠券面额
    cpValue: '',
    // 券有效开始日期
    beginDate: null,
    // 券有效截止日期
    endDate: null,
    // 查询状态
    fetching: false,
  };

  componentDidMount() {
    // 添加监听器
    this.addMessageListener();
  }

  /**
   * 添加自动流信息监听器
   */
  addMessageListener = () => {
    window.addEventListener(
      'message',
      (e) => {
        const data = e.data;
        console.log('addEventListener ');
        console.dir(data);
        if (data && data.type) {
          if (data.type === 'onInit') {
            this.onInit(data.content);
          } else if (data.type === 'onConfirm') {
            const data = this.onConfirm();
            this.sendMessage(data);
          }
        }
      },
      false,
    );
  };
  /**
   * 自动流开始加载
   * @param content 自动流参数
   */
  onInit = (content) => {
    let actionBody = content.actionBody || {};
    console.log('init message:' + JSON.stringify(actionBody));
    this.setState({
      couponsType: actionBody.couponsType,
      couponName: actionBody.couponName,
      couponQty: actionBody.couponQty,
      cpValue: actionBody.cpValue,
      beginDate: actionBody.beginDate,
      endDate: actionBody.endDate,
    });
  };

  /**
   * 点击自动流确定按钮时返回自动流的信息
   * @return
   */
  onConfirm = () => {
    if (!this.state.couponQueryValue) {
      return {
        error: {
          type: 'error',
          message: '请选择优惠券!',
        },
      };
    }
    if (!this.state.couponQty) {
      return {
        error: {
          type: 'error',
          message: '请输入优惠券数量!',
        },
      };
    }
    return {
      actionBody: {
        customerId: '${' + 'id' + '}',
        tenantId: '${' + 'tenantId!""' + '}',
        flowId: '${' + 'flowId!""' + '}',
        webhookId: '${' + 'webhookId!""' + '}',
        batchId: '${' + 'batchId!""' + '}',
        flowStep: '${' + 'flowStep!""' + '}',
        flowVersion: '${' + 'flowVersion!""' + '}',
        campaign: '${' + 'campaign!""' + '}',
        crvMemberId: '${' + 'identity.c_crv_member_id!""' + '}',
        gvMemberId: '${' + 'attr.attr18!""' + '}',
        groupName: '${' + 'lists.name!""' + '}',
        couponType: this.state.couponsType,
        couponName: this.state.couponName,
        couponQty: this.state.couponQty,
        cpValue: this.state.cpValue,
        beginDate: this.state.beginDate,
        endDate: this.state.endDate,
      },
    };
  };
  /**
   * 向自动流发送信息
   * @param content
   */
  sendMessage = (content) => {
    console.log('autoflow message:', JSON.stringify(content));
    window.parent.postMessage(
      {
        type: 'cpFinish',
        content: content,
      },
      '*',
    );
  };
  /**
   * 券查询方式选择
   * @param value
   */
  queryTypeChange = (value) => {
    this.setState({ couponQueryType: value });
  };
  /**
   * 券数量填写
   * @param value
   */
  couponQtyChange = (value) => {
    console.log('couponQty:' + value);
    this.setState({ couponQty: value });
  };
  /**
   * 券类型选择
   * @param value
   */
  queryValueChange = (value) => {
    value = value ? value.trim() : '';
    // 设置券id
    this.setState({ couponQueryValue: value });
    console.log('selected coupon type：' + JSON.stringify(value));
    this.clearValue();
    // 设置券面额，有效开始日期和有效截止日期
    if (this.queryedCouponList) {
      this.queryedCouponList.forEach((coupon) => {
        if (coupon.couponsType === value) {
          this.setState({
            couponsType: coupon.couponsType,
            couponName: coupon.couponName,
            cpValue: coupon.cpValue,
            beginDate: coupon.beginDate,
            endDate: coupon.endDate,
          });
        }
      });
    }
  };
  /**
   * 重置数据
   */
  clearValue = () => {
    this.setState({
      couponsType: null,
      couponName: null,
      couponQty: null,
      cpValue: null,
      beginDate: null,
      endDate: null,
    });
  };

  fetchCouponList = (value) => {
    if (value == undefined || value.trim() == '') {
      this.queryedCouponList = [];
      this.setState({
        couponList: this.queryedCouponList,
      });
      return;
    }
    value = value ? value.trim() : '';
    console.log('fetching coupon by keyword ,ready trim:' + value);
    this.lastFetchId += 1;
    const fetchId = this.lastFetchId;
    this.setState({ fetching: true });
    // 根据查询条件查询券列表
    couponService
      .getCouponList({
        couponQueryType: this.state.couponQueryType,
        couponQueryValue: value,
        buIdQuery: this.state.buIdQuery,
        cpValueQuery: this.state.cpValueQuery,
      })
      .then((body) => {
        if (fetchId !== this.lastFetchId) {
          return;
        }
        if (body) {
          if (body.code == 200) {
            this.queryedCouponList = body.data;
            this.queryedCouponList.forEach((value, index) => {
              value.beginDate =
                value.beginDate == null
                  ? null
                  : moment(value.beginDate, timeFormat).format(dateFormat);
              value.endDate =
                value.endDate == null
                  ? null
                  : moment(value.endDate, timeFormat).format(dateFormat);
            });
            this.setState({
              couponList: this.queryedCouponList,
            });
          } else {
            message.warning('网络繁忙，请稍后再试！');
            this.setState({
              couponList: [],
            });
          }
        }
        this.setState({
          fetching: false,
        });
      });
  };

  /**
   * BU编码选择查询
   * @param value
   */
  buIdQueryChange = (value) => {
    this.setState({ buIdQuery: value, couponQueryValue: '', couponList: [] });
  };

  /**
   * 券面额查询
   * @param value
   */
  cpValueQueryChange = (value) => {
    this.setState({
      cpValueQuery: value,
      couponQueryValue: '',
      couponList: [],
    });
  };

  render() {
    const { fetching, couponList, couponQueryValue } = this.state;
    return (
      <div className={styles.normal}>
        <div className={styles.search}>
          <span className={styles.labelTip}>快捷筛选</span>
          <Row className={styles.row}>
            <Col className={styles.col} span={5}>
              BU编码 :
            </Col>

            <Col span={10}>
              <Select
                showSearch
                defaultValue="全部"
                style={{ width: 120 }}
                placeholder="请选择"
                optionFilterProp="children"
                onChange={this.buIdQueryChange}
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
              >
                {buIdList.map((buId, index) => (
                  <Option key={index} value={buId.key}>
                    {buId.key == '' ? buId.value : buId.key + '-' + buId.value}
                  </Option>
                ))}
              </Select>
            </Col>

            <Col className={styles.col} span={4}>
              券面额(元) :
            </Col>
            <Col span={5}>
              <InputNumber
                min={0}
                defaultValue={this.state.cpValueQuery}
                onChange={this.cpValueQueryChange}
              />
            </Col>
          </Row>

          <Row className={styles.row}>
            <Col className={styles.col} span={5}>
              <span style={{ color: 'red' }}>*</span>选择优惠券 :
            </Col>
            <Col span={19}>
              <Select
                defaultValue="按券类型ID查询"
                style={{ width: 150 }}
                onChange={this.queryTypeChange}
              >
                <Option value="couponType">按券类型ID查询</Option>
                <Option value="couponName">按券名称查询</Option>
              </Select>
              <Select
                showSearch
                allowClear={true}
                //labelInValue
                value={couponQueryValue}
                placeholder="请选择..."
                notFoundContent={fetching ? <Spin size="small" /> : null}
                filterOption={false}
                onSearch={this.getCouponList}
                onChange={this.queryValueChange}
                style={{ width: 300 }}
              >
                {couponList &&
                  couponList.map((coupon) => (
                    <Option value={coupon.couponsType}>
                      <div>
                        {coupon.couponName}(
                        <span style={{ color: '#1890ff' }}>
                          {' '}
                          {coupon.couponsType}
                        </span>
                        )
                      </div>
                    </Option>
                  ))}
              </Select>
            </Col>
          </Row>
        </div>

        <div className={styles.result}>
          <span className={styles.labelTip}>发券信息</span>
          <Row className={styles.row}>
            <Col className={styles.col} span={5}>
              券名称(ID) :
            </Col>
            <Col className={styles.col_val} span={10}>
              {this.state.couponName ? this.state.couponName : ''}
              <span style={{ fontSize: 12.5, color: '#1890ff' }}>
                {this.state.couponsType
                  ? '(' + this.state.couponsType + ')'
                  : ''}
              </span>
            </Col>

            <Col className={styles.col} span={4}>
              <span style={{ color: 'red' }}>*</span>发券数量 :
            </Col>
            <Col className={styles.col_val} span={5}>
              <InputNumber
                placeholder={'请输入...'}
                min={1}
                defaultValue={this.state.couponQty}
                value={this.state.couponQty}
                onChange={this.couponQtyChange}
              />
            </Col>
          </Row>

          <Row className={styles.row}>
            <Col className={styles.col} span={5}>
              券有效日期 :
            </Col>
            <Col className={styles.col_val} span={10}>
              {this.state.beginDate == null && this.state.endDate == null
                ? ''
                : this.state.beginDate + '~' + this.state.endDate}
            </Col>

            <Col className={styles.col} span={4}>
              券面额(元) :
            </Col>
            <Col className={styles.col_val} span={5}>
              {this.state.cpValue}
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}

export default CouponIssue;
